import React from 'react';

const Footer = () => {
  return (
    <footer className="text-white bg-gradient-to-b from-[#002726] to-[#0F201E] px-6 py-10 w-full">
      <div className="max-w-[1400px] mx-auto flex flex-col md:flex-row justify-between gap-10">
        {/* Left Side: Logo + Copyright */}
       <div className="flex flex-col md:items-start items-center gap-4 w-full -pl-10">
          <img src="/images/footer-logo.png" alt="Ummah Logo" className="h-20 w-auto" />
           <p className="text-xs mt-4 md:mt-12  md:text-left text-center w-full md:w-auto order-last md:order-none">
    COPYRIGHT 2018  UNITED UMMAH
  </p>
        </div>

        {/* Right Side: 3 tightly packed columns */}
        <div className="flex flex-col sm:flex-row sm:space-x-0 gap-4 sm:gap-0  -mr-10">
          {/* Column 1: Chat Icon + Address + Buttons */}
          <div className="flex flex-col items-start gap-4 text-sm pr-4 min-w-[370px]">
            <img src="/images/chat.png" alt="Chat Icon" className="w-10 h-10" />
            <div className="leading-relaxed">
              <p>2200 South Sheridan Way</p>
              <p>Mississauga, ON L5J 2M4</p>
              <p>905.403.8406</p>
            </div>
            <div className="flex flex-col sm:flex-row gap-3  ">
              <button className="px-6 py-2 text-black bg-[#f0e4cf] font-semibold  " >
                CONTACT US
              </button>
              <button className="px-4 py-2 bg-black text-white text-xs">
                FUNERAL LINE: 1-905-403-ARAHZA
              </button>
            </div>
          </div>

          {/* Column 2: Mosque Icon + Location Text */}
          <div className="flex flex-col items-start gap-4 text-sm px-4 min-w-[200px]">
            <img src="/images/mos.png" alt="Mosque Icon" className="w-10 h-10" />
            <div className="leading-relaxed">
              <p>Mississauga - ICC</p>
              <p>Toronto - Jami Mosque</p>
              <p>Yellowknife - ICYK</p>
              <p>Youth Hub - ICC</p>
            </div>
          </div>

          {/* Column 3: Social Icons */}
          <div className="flex flex-row md:flex-col  md:items-start  gap-5 text-sm pl-4 w-full md:w-auto min-w-[200px]">
            <img src="/images/fb.png" alt="Facebook" className="w-7 h-7" />
            <img src="/images/insta.png" alt="Instagram" className="w-7 h-7" />
            <img src="/images/yt.png" alt="YouTube" className="w-7 h-7" />
            <img src="/images/twitter.png" alt="Twitter" className="w-7 h-7" />
          </div>
        </div>
         
         
      </div>
   
    </footer>
  );
};

export default Footer;
